width:-moz-fit-content;width:fit-content;border:none;background-color:transparent;font-family:inherit;cursor:pointer;transition:background-color .3s linear,color .3s linear,transform .3s linear;margin:0;padding:0;border-radius:7px}button:hover,select:hover,.button:hover{background-color:#e6e6e6;color:#323232}button svg,select svg,.button svg{transition:inherit;height:2rem}button,.button{justify-content:center;align-items:center;display:flex}select{-webkit-appearance:none;-moz-appearance:none;border:1px solid grey;background-image:url("data:image/svg+xml;utf8,<svg fill='black' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");background-repeat:no-repeat;background-position-x:100%;background-position-y:50%;overflow:hidden;text-overflow:ellipsis;padding-right:1rem;padding-left:0.5rem;font-family:inherit;cursor:pointer}select:hover{border:1px solid transparent}select *{font-family:inherit;background-color:white}a.unstyled{color:inherit;text-decoration:none}

