on-content{display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:auto;grid-column-gap:3rem;-webkit-column-gap:3rem;column-gap:3rem;align-items:center}#detail-view #detail-data .stat-section-content .stat-container{display:grid;grid-template-columns:2.5rem 1.5rem auto;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem}#detail-view #detail-data .stat-section-content .stat-container .label{font-weight:600}#detail-view #detail-data .stat-section-content .stat-container .figure{text-align:right}#detail-view #detail-data .stat-section-content .stat-container .progress-bar{background-color:#e6e6e6;height:1rem;width:100%;border-radius:50px}#detail-view #detail-data .stat-section-content .stat-container .progress-bar div{background-color:cornflowerblue;height:100%;border-radius:inherit}@media screen and (max-width: 768px){#detail-view #detail-data .stat-section-content{grid-template-columns:1fr}}

