#poke-list{list-style-type:none;margin:0;padding:0.25rem;position:fixed;-webkit-backface-visibility:hidden;top:4rem;left:0;bottom:0;background-color:white;overflow-y:scroll;overflow-x:hidden;width:3.5rem;box-shadow:0 5px 5px #2a2a2a;z-index:20;transition:width 0.3s ease-in-out}#poke-list:hover,#poke-list:focus-within{width:14rem}#poke-list .search-bar{margin:0.25rem 0;grid-gap:1rem;gap:1rem}#poke-list li{font-size:1rem;cursor:pointer;display:flex;align-items:center;transition:background-color .1s linear;height:2.5rem;width:100%;border-top:1px solid #e0e0e0}#poke-list li>*{margin-bottom:0;margin-right:.75rem}#poke-list li>*:last-child{margin-right:0}#poke-list li:hover{background-color:#e0e0e0}#poke-list li:first-child{border:none;background-color:transparent}#poke-list li.selected{background-color:#ef5350;border-radius:10px;color:white}#poke-list li.selected:hover{background-color:#c62828}#poke-list li a{display:flex;align-items:center}#poke-list li a>*{margin-bottom:0;margin-right:.75rem}#poke-list li a>*:last-child{margin-right:0}#poke-list li .pokeOverview{display:flex;white-space:nowrap}#poke-list li .pokeOverview>*{margin-bottom:0;margin-right:.5rem}#poke-list li .pokeOverview>*:last-child{margin-right:0}#poke-list li .pokeOverview .pokeName,#poke-list li .pokeOverview .pokeId{font-size:1rem;font-weight:normal;font-family:'Nova Mono'}#poke-list li .pokeOverview .pokeName{padding-right:1rem}

#generations-section{display:grid;padding:3% 5%;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 25vw);grid-gap:2rem;gap:2rem;position:absolute;top:0;left:0;right:0;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}#generations-section .generation-box{background-color:#f5f5f5;position:relative;border-radius:10px;box-shadow:0 2px 5px grey;justify-content:center;align-items:center;display:flex;transition:box-shadow .3s linear,-webkit-filter .3s linear;transition:box-shadow .3s linear,filter .3s linear;transition:box-shadow .3s linear,filter .3s linear,-webkit-filter .3s linear;cursor:pointer}#generations-section .generation-box:hover{box-shadow:0 2px 3px grey;-webkit-filter:brightness(0.9);filter:brightness(0.9)}#generations-section .generation-box .background-patterns{overflow:hidden}#generations-section .generation-box .background-patterns img[name="pokeball"]{height:130%;width:auto;bottom:-30%;right:-15%;opacity:0.3;transform:rotate(10deg)}#generations-section .generation-box .background-patterns svg[name="dots-1"]{bottom:0.5rem;left:0.5rem;transform:rotate(90deg);fill:black;height:5rem;height:30%;opacity:0.2}#generations-section .generation-box .starter{position:absolute;width:35%}#generations-section .generation-box .starter:nth-of-type(1){left:10%;bottom:30%}#generations-section .generation-box .starter:nth-of-type(2){position:relative;align-self:center;z-index:5}#generations-section .generation-box .starter:nth-of-type(3){right:10%;bottom:30%}#generations-section .generation-box .generation-heading{position:absolute;bottom:0;font-family:inherit;font-weight:bold;width:100%;height:15%;justify-content:center;align-items:center;display:flex;padding:0.5rem;background-color:rgba(0,0,0,0.5);color:white;border-bottom-left-radius:inherit;border-bottom-right-radius:inherit;box-shadow:inherit}#generations-section .generation-box .generation-heading>*{margin-bottom:0;margin-right:.5rem}#generations-section .generation-box .generation-heading>*:last-child{margin-right:0}

@keyframes loader{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}.loader{justify-content:center;align-items:center;display:flex;position:relative;width:100%;height:100%}.loader>*{margin-bottom:0;margin-right:.5rem}.loader>*:last-child{margin-right:0}.loader .loader-piece{width:16px;background-color:#ef5350;animation:loader 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}.loader .loader-piece:nth-child(1){left:8px;animation-delay:-0.24s;border-top-left-radius:30px;border-bottom-left-radius:30px;width:24px}.loader .loader-piece:nth-child(2){left:32px;animation-delay:-0.12s;width:24px}.loader .loader-piece:nth-child(3){left:56px;animation-delay:0;width:40px}.loader .progress-bar{width:50%;height:2rem;background-color:#d6d6d6;border-radius:30px;position:relative;transform:translateX(-1.5rem);box-shadow:0.5rem 0 0 0.9rem white}.loader .progress-bar .loaded-section{background-color:#ef5350;height:100%;position:absolute;left:0;top:0;border-radius:inherit;transition:ease}

